#root {
  position: absolute;
  width: 100%;
}

.logo {
  image-rendering: optimizeSpeed;
  width: 48px;
  height: 48px;
  padding: 4px;
}

.acitem {
  margin-left: 4px;
  margin-top: 8px;
}

.acitem:hover {
  background-color: #EEE;
}

code,
pre {
  background: #eee;
}

code {
  padding: 2px 4px;
  vertical-align: text-bottom;
}

pre {
  padding: 1em;
}

body {
  font-family: "Helvetica", "Arial", sans-serif;
}

body {
  line-height: 1.5;
}


h1,
h2,
strong {
  color: #333;
}

body {
  color: #566b78;
}

code,
pre {
  background: #f5f7f9;
  border-bottom: 1px solid #d8dee9;
  color: #a7adba;
}

pre {
  border-left: 2px solid #69c;
}